* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: Arial, sans-serif;
}
.App {
  background-image: url(../pictures/hot.jpg);
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}
.App.cold {
  background-image: url(../pictures/cold.jpg);
}
.wrapper {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  display: grid;
  grid-template-columns: 1fr 400px 400px 1fr;
  grid-template-rows: 100px 300px 350px 200px;
}
.searchSpace {
  margin-bottom: 5%;
  width: 100%;
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 3;
  grid-column-end: 4;
  z-index: 2;
}
.searchSpace input {
  display: block;
  min-width: 15%;
  margin: 15px auto;
  border-radius: 16px;
  padding: 15px;
  font-size: 1rem;
  border: none;
  outline: none;
  background: none;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 3px 5px rgba(0, 0, 0, 0.2);
}
.informationBox {
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 2;
  grid-column-end: 4;
  display: grid;

  grid-template-columns: 400px 400px;
  grid-template-rows: 300px 350px;
}
.locationInfo {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
  text-align: center;
  padding-top: 15%;
}
.locationInfo span:nth-child(1) {
  font-size: 65px;
  display: block;
  color: #fff;
  padding: 15px 25px;
  font-weight: 500;
  text-shadow: 3px 4px rgba(50, 50, 70, 0.5);
}
.locationInfo span:nth-child(2) {
  font-size: 35px;
  display: block;
  color: #fff;
  font-style: italic;
  padding: 15px 25px;
  font-weight: 900;
  text-shadow: 3px 4px rgba(50, 50, 70, 0.5);
}
.weatherInfo {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 2;
  line-height: 150px;
  text-align: center;
}
.weatherInfo span:nth-child(1) {
  width: 90%;
  color: #fff;
  display: block;
  margin: 30px auto 0;
  font-size: 102px;
  border-radius: 16px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 15px 25px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
}
.weatherInfo span:nth-child(2) {
  color: white;
  display: block;
  font-size: 4rem;
  margin: 0 auto;
  font-weight: 700;
}
.searchSpace input:focus {
  background-color: rgba(255, 255, 255, 0.75);
}
.locationInfo span:nth-child(4) {
  color: white;
}
